* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  }

  ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .main-container {
    padding: 12px;
    padding-top: 5rem;
    
  }

  .show-transition{
    opacity: 0;
    transform: translateX(-10px);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }

  .show-transition.show {
    opacity: 1;
    transform: translateX(0);
  }
  

  /* HEADING */

  .heading {
    text-align: center;
  }

  .heading__title {
    font-weight: 600;
  }

  .heading__credits {
    margin: 10px 0px;
    color: #888888;
    font-size: 25px;
    transition: all 0.5s;
  }

  .heading__link {
    text-decoration: none;
    cursor: pointer;
  }

  .vertical_align_middle {
    vertical-align: middle;
  }

  .heading__credits .heading__link {
    color: inherit;
  }
  
  /* CARDS */

  .cards_ct {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .card_ct {
    margin: 6px;
    padding: 6px;
    width: 500px;
    min-height: 200px;
    display: grid;
    /* grid-template-rows: 20px 50px 1fr 1fr 50px; */
    grid-template-rows: 20px 50px 1.7rem 1fr 50px;
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
  }

  .card_ct:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    transform: scale(1.01);
  }

  .card__link,
  .card__fee,
  .card__icon {
    position: relative;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.9);
    cursor: pointer;
  }

  .card__ratings {
    align-items: flex-start;
    display: flex;
    flex-wrap: nowrap;
    list-style: none;
    margin: 1rem 0;
    overflow-x: scroll;
    padding: 0 0 0 1rem;
    /* scroll-padding-left: 1rem; */
    scroll-snap-type: x mandatory;
    /* Hide on Firefox */
    scrollbar-width: none
  }

  /* Hide on WebKit browsers */
  .card__ratings::-webkit-scrollbar {
    display: none;
  }

  .card__link::after {
    position: absolute;
    top: 25px;
    left: 0;
    content: "";
    width: 0%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.6);
    transition: all 0.5s;
  }

  .card__link:hover::after {
    width: 100%;
  }

  .card__fee {
    grid-row: 1/2;
    justify-self: end;
    font-size: 0.8rem;
    white-space: nowrap;
  }

  .card__icon {
    grid-row: 2/3;
    font-size: 1.5rem;
    font-weight: 100;
    text-transform: capitalize;
  }

  .card__title {
    grid-row: 3/4;
    font-weight: 500;
    color: #ffffff;
    font-size: 1.3rem;
    text-transform: capitalize;
  }

  .card__ratings {
    grid-row: 4/5;
    align-self: center;
  }

  .ratings{
    border-radius: 12px;
    background-color: lightgoldenrodyellow;
    font-size: 0.8rem;
    padding: 5px;
    list-style-type: none;
    /* float: left; */
    margin: 5px;
    color: black;
    /* font-weight: 400; */
    /* padding: 0 1rem 0 0; */
    scroll-snap-align: start;
    scroll-snap-stop: always;
    white-space: nowrap;
    cursor: pointer;
  }

  .card__apply {
    grid-row: 5/6;
    align-self: center;
  }

  /* CARD BACKGROUNDS */

  .card-1 {
    background: radial-gradient(circle,#3fbafe,#3d8a90);
  }

  .card-2 {
    background: radial-gradient(circle, #fa99b2, #cc697c);
  }

  .card-3 {
    background: radial-gradient(circle, #b69efe, #453670);
  }

  .card-4 {
    background: radial-gradient(circle, #368368, #27655f);
  }

  .card-5 {
    background: radial-gradient(circle, #a184c5, #763064);
  }

  .card-6 {
    background: radial-gradient(#1fe4f5, #3fbafe);
  }

  .card-7 {
    background: radial-gradient(#fbc1cc, #fa99b2);
  }

  .card-8 {
    background: radial-gradient(#76b2fe, #b69efe);
  }

  .card-9 {
    background: radial-gradient(#60efbc, #58d5c9);
  }

  .card-0 {
    background: radial-gradient(circle, #f14e4e, #b04545);
  }

  
  .profile__image{
    width: 5em;
    height: 5em;
    border-radius: 50%;
    object-fit: cover;
  }

  .detail_page__image{
    width: 17rem;
    height: 17rem;
    border-radius: 50%;
    object-fit: cover;
  }

  .thumbnail{
    width: 45px;
    height: 45px;
    object-fit: cover;
  }

  .circle{
    border-radius: 50%;
  }

  .input_radius{
    border-radius: 25px;
  }
  .clear_outer{
    cursor: pointer;
    position: absolute;
    right: 0;
    z-index: 1000;
  }

  .clear_inner{
    display: flex;
    align-items: center;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    white-space: nowrap;
    background-color: #e9ecef;
    border: 1px solid #ced4da;
    border-radius: 50%;
  }

  .custom-search-input{
    border-radius: 20px !important;
  }
  
  #searchDiv ul > li:nth-child(2n+2){
    background:#e6f0f4;
  }

  #searchDiv ul > li:hover{
    background:lightblue;
    transform: scale(1.02);
  }

  .filtered-box{
    position: absolute;
    z-index: 1;
    transition: 0.7s;
    font-size: medium;
    top: 2.4rem;
    width: 100%;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    transform: scale(1.01);
    cursor: pointer;
    border-radius: 10px;
  }

  .center{
    margin:0;
    height:100vh; /* use 100% of the height of the viewport */
    display:grid;             
    place-items: center;
  }

  .capitalize{
    text-transform: capitalize;
  }

  .capitalize-first::first-letter {
    text-transform: uppercase;
  }

  .clear:hover{
    transform: scale(1.02);
  }

  .close{
    border: 0;
    padding: 5px;
    border-radius: 5px;
    margin-right: 2px;
  }

  .pl-1{
    padding-left: 1rem;
  }

  .card-plan {
    margin: 6px;
    padding: 6px;
    width: 500px;
    min-height: 200px;
    display: grid;
    /* grid-template-rows: 20px 50px 1fr 1fr 50px; */
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
  }


  .otpInput {
    width: 1.7rem !important;
    height: 2.1rem;
    margin-right: .5rem;
    font-size: 1rem;
    text-align: center;
    border-radius: 10px !important;
    border: 1px solid rgba(0, 0, 0, 0.3);
  }

  .list-scrollable {
    max-height: 20rem; /* Set the desired height */
    overflow-y: scroll; /* Enable vertical scrolling */
    overflow-x: hidden;
    border-radius: 10px;
  }
  
  .list-scrollable::-webkit-scrollbar {
    width: 0.1em; /* Set the width of the scrollbar */
  }
  
  .list-scrollable::-webkit-scrollbar-track {
    background-color: #f1f1f1; /* Set the background color of the scrollbar track */
  }
  
  .list-scrollable::-webkit-scrollbar-thumb {
    background-color: #888; /* Set the color of the scrollbar thumb */
  }
  
  .list-scrollable::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Set the color of the scrollbar thumb on hover */
  }

  .disabled-bg-color{
    background-color: #e9ecef;
  }
  


  /* RESPONSIVE */

  @media (max-width: 1600px) {
    .cards_ct {
      justify-content: center;
    }
  }

  @media (min-width: 752px) {
    .card_ct{
      grid-template-rows: 20px 50px 1fr 1fr 50px;
    }
  }
